// R3 - Pending changes

import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { logout } from 'reducers/auth/action-creators'
import { connect } from 'react-redux'

import AppBar from '@material-ui/core/AppBar'
import { unstable_Box as Box } from '@material-ui/core/Box'

import { withStyles } from '@material-ui/styles'

import { compose } from 'recompose'
import MenuIcon from '@material-ui/icons/Menu'
import { Grid, Typography, IconButton, Avatar, Hidden } from '@material-ui/core';

const styles = {
  appBar: {
    zIndex: 0,
  },
}

const Header = (props) => {
  const {
    logout,
    onDrawerToggle,
    auth,
    activeItem,
  } = props

  const [setAnchorEl] = useState(null)

  const handleClose = () => {
    setAnchorEl(null)
  }

  const handleLogout = () => {
    logout()
    handleClose()
  }

  return(
      <AppBar
        color='primary'
        component={Box}
        elevation={0}
        paddingTop={3}
        position='sticky'
      >
        <Grid
            alignItems='center'
            container
            spacing={8}
        >
          <Hidden smUp>
                <Grid item>
                  <IconButton
                    aria-label='Open drawer'
                    color='inherit'
                    onClick={onDrawerToggle}
                  >
                    <MenuIcon />
                  </IconButton>
                </Grid>
          </Hidden>
          <Grid item>
            <Typography color='inherit' variant="h4" gutterBottom>
              &nbsp;&nbsp;{activeItem}
            </Typography>
          </Grid>
          <Grid item xs />
          <Hidden mdDown>
            <Grid item>
              <Typography color='inherit' variant="h5">Bem Vindo(a)!</Typography>
            </Grid>
            <Grid item>
              <Avatar src={auth.info.image}/>
            </Grid>
          </Hidden>
        </Grid>
      </AppBar>
  )
}

Header.propTypes = {
  logout: PropTypes.func.isRequired,
  onDrawerToggle: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  activeItem: PropTypes.string.isRequired,
  handleTabOpen: PropTypes.func.isRequired,
  tabOpen: PropTypes.number.isRequired,

  // Check if this prop is correct later
  tabStruct: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string.isRequired,
    })
  ),
}

const mapStateToProps = ({ auth }) => ({ auth })

const mapDispatchToProps = dispatch => ({
  logout: () => dispatch(logout()),
})

export default compose(
  withStyles(styles),
  connect(mapStateToProps, mapDispatchToProps),
)(Header)
