// R3 - Pending changes

import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { openSnackbar } from 'reducers/snackbar/action-creators'
import { getContent, filterContent, filterContentSuccess} from 'reducers/firestore/action-creators'
import { openForm } from 'reducers/content-form/action-creators'
import { connect } from 'react-redux'

import Paper from '@material-ui/core/Paper'
import { unstable_Box as Box } from '@material-ui/core/Box'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Grid from '@material-ui/core/Grid'
import SearchIcon from '@material-ui/icons/Search'
import TextField from '@material-ui/core/TextField'
import Typography from '@material-ui/core/Typography'

import styles from './styles'
import { withStyles } from '@material-ui/styles'

import imgurUtils from 'utils/imgur'
import FormDialog from './FormDialog.js'
import { compose } from 'recompose'
import { Card, CardContent, CardMedia, List } from '@material-ui/core';

const Homepage = (props) => {
  const {
    type,
    getContent,
    firestore,
    openSnackbar,
    filterContent,
    filterContentSuccess,
    classes,
    ManagerForm,
    managerValidation,
  } = props

  // Menu state
  const [anchorEl, setAnchorEl] = useState(null)
  const openMenu = Boolean(anchorEl)

  // Choosed content state
  const [contentIndex, setContentIndex] = useState(null)

  useEffect(() => {
    getContent(type)
  }, [type])

  const styles = theme => ({
    cover: {
      width: '200px',
      height: '200px'
    },
    details: {
      display: 'flex',
      flexDirection: 'column',
    },
    content: {
      display: 'flex',
      flex: '1 0 auto',
      alignContent: 'center',
    },
    controls: {
      display: 'flex',
      alignItems: 'center',
      paddingLeft: theme.spacing.unit,
      paddingBottom: theme.spacing.unit,
    },
    playIcon: {
      height: 38,
      width: 38,
    },
  });

  const {
    error,
    filter,
    loading,
  } = firestore

  useEffect(() => {
    if (error) {
      openSnackbar({
        type: 'error',
        content: error,
        duration: 6000,
      })
    }
  }, [error])

  const contents = firestore[`${firestore.filter ? 'filteredContent' : 'contents'}`]

  const handleMenu = (e) => {
    setAnchorEl(e.currentTarget)
  }

  const handleContent = (index) => {
    setContentIndex(index)
  }

  const handleFilter = (e) => {
    const { target: { value } } = e
    value ? filterContent(value) : filterContentSuccess()
  }

  return (
    <div>
    <Paper
      className={[classes.root, classes.searchCard].join(' ')}
      margin='auto'
    >
      <AppBar
        component={Box}
        borderBottom='1px solid rgba(0, 0, 0, 0.12)'
        position='static'
        color='default'
        elevation={0}
      >
        <Toolbar>
          <Grid
            alignItems='center'
            container
            spacing={0}
          >
            <Grid item>
              <Box
                color='inherit'
                component={SearchIcon}
                display='block'
              />
            </Grid>
            <Grid item xs>
              <TextField
                disabled={!filter && (loading || contents.length <= 0)}
                fullWidth
                InputProps={{
                  className: classes.input,
                  disableUnderline: true,
                }}
                onChange={handleFilter}
                placeholder='Pesquise aqui...'
              />
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
    </Paper>

    {
        ManagerForm && managerValidation
          ? (
            <FormDialog
              type={type}
              ManagerForm={ManagerForm}
              managerValidation={managerValidation}
            />
          ) : null}
      <List className='loader'>
        {
          loading
            ? null
            : (
              contents.length <= 0
                ? (
                  <Box px={2} py={9.25}>
                    <Typography color='textSecondary' align='center'>
                      Nenhum (a) {
                        type === 'job'
                          ? 'vaga de emprego'
                          : type === 'culture'
                            ? 'dica cultural'
                            : type === 'course'
                              ? 'curso'
                              : type === 'video'
                                ? 'videoaula'
                                : type === 'game'
                                  ? 'jogo'
                                  : null
                      } foi encontrado (a)
                    </Typography>
                  </Box>
                )
                : contents.map(({ uid, image, name, date, url, description }, index) => (
                  <Card 
                    className={classes.card} aria-haspopup='true'
                    aria-owns={openMenu ? 'menu-content' : undefined}
                    rounded
                    elevation={1}
                    button
                    key={uid}
                    onClick={url ? ()=> window.open(`${url}`, "_blank") : null}
                     >
                    <CardMedia
                      className={styles.cover}
                      image={imgurUtils.get(image).url}
                      style={{height:'250px', width:'300px'}}
                    />
                    <CardContent className={styles.content}>
                      <Typography component="h5" variant="h5">
                        {name}
                      </Typography>
                      <Typography variant="subtitle1" color="textSecondary">
                        {description ? description : date ? date : null} 
                      </Typography>
                    </CardContent>
                  </Card>
                ))
            )
        }
      </List>

    
    </div>
  )
}

Homepage.defaultProps = {
  formSteps: 1,
  Form: null,
  stepValidate: null,
}

Homepage.propTypes = {
  type: PropTypes.oneOf(['job', 'culture', 'course', 'video', 'game']),
  getContent: PropTypes.func.isRequired,
  firestore: PropTypes.object.isRequired,
  openSnackbar: PropTypes.func.isRequired,
  formSteps: PropTypes.number,
  filterContent: PropTypes.func.isRequired,
  filterContentSuccess: PropTypes.func.isRequired,
  openForm: PropTypes.func.isRequired,
  classes: PropTypes.object.isRequired,
  ManagerForm: PropTypes.any,
  managerValidation: PropTypes.func,
}

const mapStateToProps = ({ firestore }) => ({ firestore })

const mapDispatchToProps = dispatch => ({
  getContent: type => dispatch(getContent({ type })),
  openSnackbar: options => dispatch(openSnackbar(options)),
  filterContent: query => dispatch(filterContent(query)),
  filterContentSuccess: () => dispatch(filterContentSuccess()),
  openForm: options => dispatch(openForm(options)),
})

export default compose(
  withStyles(styles),
  connect(mapStateToProps, mapDispatchToProps),
)(Homepage)
